.input {
	font-size: 18px;

	background-color: var(--white-color);
	outline: none;
	transition: box-shadow var(--hover-time) ease;
}

.input.sale {
	max-width: 475px;
	width: 100%;
	height: 75px;
	padding: 25px 50px;
	font-weight: 600;
	border: none;
	border-radius: 25px;
	color: var(--6c-color);
}

.input.order {
	max-width: 446px;
	width: 100%;
	height: 72px;
	padding: 25px;
	font-weight: 500;
	border-radius: 20px;
	color: var(--92-color);
	border: 1px solid var(--black-color);
}

@media (min-width: 762px) {

	input:focus {
		box-shadow: inset 0 0 19px var(--black-color);
	}

	input:hover {}

	input:active {}

}

@media (max-width: 762px) {
	.input.sale {
		max-width: 100%;
	}
}